@import url('https://fonts.googleapis.com/css?family=Open+Sans');

.App {
  text-align: center;
  font-family: 'Open Sans', sans-serif;
}

.main {
  width: calc(100vw - 50px);
  justify-content: center;
  margin: 0 auto;
  margin-bottom: 25px;
}

.project {
  position: relative;
}

.projectname {
  font-size: 16pt;
  font-weight: bold;
  margin-bottom: -20pt;
}

.projectskills {
  font-size: 9pt;
  font-style: italic;
}

.projectpreview {
  font-size: 12pt;
  margin-top: -20px;
}

.actions {
  font-size: 10pt;
  height: 10pt;
  position: absolute;
  bottom: 0;
  background-color: rgba(255,255,255, 0.7);
}

/* min-height finagling for project cards: still in early stages */

.projectgrid:nth-child(-n+6) > .project { /* first six cards */
  min-height: 400px;
}

.projectgrid:nth-child(n+7) > .project { /* seventh and up cards */
  min-height: 600px;
}

@media screen and (max-width: 900px) {
  .projectgrid:nth-child(n+9):nth-child(-n+10) > .project { /* seventh and up cards */
    min-height: 718px;
  }
}